<template>
    <div class="jp-menuList">
        <!---->
        <div class="menu-con">
            <p class="title"></p>
            <div class="menu-list">
                <div class="item">
                    <div class="item-con"></div>
                    <div class="item-ipt">
                        <el-input v-model.trim="name" placeholder="" @keyup.enter.native="onSearch($event)"></el-input>
                    </div>
                </div>
                <div class="item">
                    <div class="item-con"></div>
                    <div class="item-ipt">
                        <el-select v-model="projectID" filterable placeholder="">
                            <el-option v-for="(val,index) in projectList" :key="val.id" :label="val.name" :value="val.id"></el-option>
                        </el-select>
                    </div>
                </div>
                <div class="item">
                    <button class="search" @click="onSearch"></button>
                    <button class="add" @click="dialogAdd = true"></button>
                </div>
            </div>
        </div>
        <!---->
        <div class="data" v-loading="loading" element-loading-text="">
            <table>
                <thead>
                    <tr>
                        <td></td>
                        <td></td>
                        <td></td>
                        <td></td>
                        <td></td>
                    </tr>
                </thead>
                <tbody>
                    <tr v-for="(item,index) in list" :key="index">
                        <td>{{(curPage-1)*10 + index + 1}}</td>
                        <td v-html="item.name"></td>
                        <td v-html="item.sysItemList.name"></td>
                        <td>{{item.status == 1 ? '' : ''}}</td>
                        <td>
                            <el-button class="edit-btn" type="text" @click="edit(item.id)"></el-button>
                            <el-button class="del-btn" type="text" @click="del(item.id)"></el-button>
                        </td>
                    </tr>
                    <no-data v-if="flag"></no-data>
                </tbody>
            </table>
            <div id="page">
                <rt-page ref="page" :cur="curPage" :total="total" @change="pageHandler"></rt-page>
            </div>
        </div>
        <!---->
        <el-dialog class="xinjian" title="" :visible.sync="dialogAdd">
            <el-form v-loading="loadingNew" :model="newBuild" :rules="rulesAdd" ref="newBuild">
                <el-form-item label="" :label-width="formLabelWidth" prop="projectID">
                    <el-select v-model="newBuild.projectID" placeholder="" @change="selNewProject">
                        <el-option v-for="(val,index) in projectList" :key="val.id" :label="val.name" :value="val.id"></el-option>
                    </el-select>
                </el-form-item>
                <el-form-item label="" :label-width="formLabelWidth" prop="moduleID">
					<el-select v-model="newBuild.moduleID" placeholder="">
                        <el-option v-for="(val,index) in nameList" :key="val.id" :label="val.name" :value="val.id"></el-option>
                    </el-select>
                </el-form-item>
                <el-form-item label="" :label-width="formLabelWidth" prop="parentMenuID">
                    <el-select v-model="newBuild.parentMenuID" placeholder="">
                        <el-option v-for="(val,index) in parentMenuLists" :key="val.id" :label="val.name" :value="val.id"></el-option>
                    </el-select>
                </el-form-item>
                <el-form-item label="" :label-width="formLabelWidth">
                    <el-radio class="radio" v-model="newBuild.radio" label="1"></el-radio>
                    <el-radio class="radio" v-model="newBuild.radio" label="0"></el-radio>
                </el-form-item>
                <el-form-item label="" :label-width="formLabelWidth" prop="memo">
                    <el-input type="textarea" :autosize="{ minRows: 2, maxRows: 4}" placeholder="" v-model.trim="newBuild.memo">
                    </el-input>
                </el-form-item>
                <el-form-item class="btn">
                    <el-button class="save" type="primary" @click="addSAVE('newBuild')"></el-button>
                    <el-button class="off" @click="addOFF('newBuild')"></el-button>
                </el-form-item>
            </el-form>
        </el-dialog>
        <!---->
        <el-dialog class="xinjian" title="" :visible.sync="dialogEdit" :before-close="handleClose">
            <el-form v-loading="loadingEdit" :model="editDialog" :rules="rulesEdit" ref="editDialog">
                <el-form-item label="" :label-width="formLabelWidth" prop="projectID">
                    <el-select v-model="editDialog.projectID" filterable :placeholder="editDialog.projectName" @change="selEditProject">
                        <el-option v-for="(val,index) in projectList" :key="val.id" :label="val.name" :value="val.id"></el-option>
                    </el-select>
                </el-form-item>
                <el-form-item label="" :label-width="formLabelWidth" prop="moduleID">
                    <el-select v-model="editDialog.moduleID" :placeholder="editDialog.moduleName">
                        <el-option v-for="(val,index) in editNameList" :key="val.id" :label="val.name" :value="val.id"></el-option>
                    </el-select>
                </el-form-item>
                <el-form-item label="" :label-width="formLabelWidth">
                    <el-select v-model="editDialog.parentMenuID" filterable :placeholder="editDialog.parentMenuName">
                        <el-option v-for="(val,index) in parentMenuList" :key="val.id" :label="val.name" :value="val.id"></el-option>
                    </el-select>
                </el-form-item>
                <el-form-item label="" :label-width="formLabelWidth" prop="radio">
                    <el-radio-group v-model="editDialog.radio" @change="selRadio">
                        <el-radio :label="1"></el-radio>
                        <el-radio :label="0"></el-radio>
                    </el-radio-group>
                    <p v-if="radioFlag" v-html="radioCon"></p>
                </el-form-item>
                <el-form-item label="" :label-width="formLabelWidth" prop="memo">
                    <el-input type="textarea" :autosize="{ minRows: 2, maxRows: 4}" placeholder="" v-model.trim="editDialog.memo">
                    </el-input>
                </el-form-item>
                <el-form-item class="btn">
                    <el-button class="save" type="primary" @click="editSAVE('editDialog')"></el-button>
                    <el-button class="off" @click="editOFF('editDialog')"></el-button>
                </el-form-item>
            </el-form>
        </el-dialog>
    </div>
</template>
<script>
    import rtPage from '@/components/pagination'
    import noData from '@/components/noData'
    import md5 from 'js-md5'
    export default {
        data() {
            //
            var patternNameNew = (rule, value, callback) => {
                if(value == "") {
                    callback("")
                } else {
                    let data = {
                        moduleId: this.newBuild.moduleID,
                        itemId: this.newBuild.projectID
                    }
                    this.axios.post("/menu/selectMenuByName", data)
                        .then((res) => {
                            if(res.data.data) {
                                callback()
                            } else {
                                callback("")
                            }
                        }).catch((error) => {
                            console.log(error);
                        })
                }
            };
            //
            var patternProjectIDNew = (rule, value, callback) => {
                if(value == "") {
                    callback("")
                } else {
                    callback()
                }
            };
            //
            var patternNameEdit = (rule, value, callback) => {
                if(value == "") {
                    callback("")
                } else {
                    if(this.editDialog.moduleID == this.editName && this.editDialog.projectID == this.editID) {
                        callback();
                    } else {
                        let data = {
                            moduleId: this.editDialog.moduleID,
                            itemId: this.editDialog.projectID
                        };
                        this.axios.post("/menu/selectMenuByName", data)
                            .then((res) => {
                                if(res.data.data) {
                                    callback()
                                } else {
                                    callback("")
                                }
                            }).catch((error) => {
                                console.log(error);
                            })
                    }
                }
            };
            //
            var patternProjectIDEdit = (rule, value, callback) => {
                if(value == "" || value == null) {
                    callback("")
                } else {
                    callback()
                }
            };
            return {
                //
                props: {
                    value: 'id',
                    label: 'name',
                    children: 'children'
                },
                //,
                loadingNew: false,
                loadingEdit: false,
                //
                flag: false,
                //
                loading: false,
                /*id*/
                name: '',
                projectID: '',
                searchStatus: 0,
                /*id*/
                menuID: '',
                /*,*/
                nameList: [],
                editNameList: [],
                projectList: [],
                parentMenuList: [],
                parentMenuLists: [],
                /**/
                dialogAdd: false,
                dialogEdit: false,
                /**/
                total: 0,
                curPage: 1,
                list: "",
                /**/
                newBuild: {
                    name: '',
                    moduleName: '',
                    moduleID: '',
                    projectID: '',
                    parentMenuID: '',
                    radio: '1',
                    memo: ''
                },
                /**/
                editDialog: {
                    name: '',
                    moduleID: '',
                    moduleName: '',
                    projectName: '',
                    projectID: '',
                    parentMenuName: '',
                    parentMenuID: '',
                    radio: '',
                    memo: ''
                },
                formLabelWidth: '100px',
                //
                editName: '',
                editNameID: '',
                editID: '',
                newEditID: '',
                /**/
                rulesAdd: {
                    projectID: [{
                        validator: patternProjectIDNew,
                        required: true,
                    }],
                    moduleID: [{
                        type: 'array',
                        validator: patternNameNew,
                        required: true,
                    }],
                    memo: [{
                        max: 30,
                        message: '  30',
                        trigger: 'blur'
                    }]
                },
                /**/
                rulesEdit: {
                    projectID: [{
                        validator: patternProjectIDEdit,
                    }],
                    moduleID: [{
                        type: 'array',
                        validator: patternNameEdit,
                        required: true
                    }],
                    memo: [{
                        max: 30,
                        message: '  30',
                        trigger: 'blur'
                    }]
                },
                radio: '',
                radioFlag: false,
                radioCon: ''
            }
        },
        components: {
            rtPage,
            noData
        },
        mounted() {
            this.getProject();
            this.getParentMenu();
        },
        methods: {
            //
            handleClose(done) {
                done();
                this.editEmpty();
            },
            handleChange(value) {},
            //
            ajaxData(data) {
                this.axios.post("/menu/getMuneDetailsByLike", data)
                    .then((res) => {
                        this.loading = false
                        if(res.data.data == null) {
                            this.flag = true;
                            this.list = '';
                            this.total = 0;
                        } else {
                            this.flag = false
                            this.list = res.data.data.list;
                            this.total = Number(res.data.data.total);
                        }
                    }).catch((error) => {
                        console.log(error);
                        this.flag = true;
                        this.loading = false
                    })
            },
            //
            pageHandler(curPage) {
                //
                this.loading = true;
                //
                this.curPage = curPage;
                let data = {
                    name: this.searchStatus == 0 ? '' : this.name, //
                    itemId: this.searchStatus == 0 ? '' : this.projectID, //id
                    pageIndex: this.curPage, //
                    size: 10
                };
                this.ajaxData(data);
            },
            //
            onSearch(ev) {
                //
                var aa = /[@#\$%\^&\*<>!(),\.]+/g;
                if(aa.test(this.name) == true) {
                    alert("")
                } else {
                    this.loading = true;
                    //
                    this.total = 0;
                    this.searchStatus = 1;
                    let data = {
                        name: this.searchStatus == 0 ? '' : this.name, //
                        itemId: this.searchStatus == 0 ? '' : this.projectID, //id
                        pageIndex: this.searchStatus == 0 ? '' : this.curPage, //
                        size: 10
                    };
                    this.ajaxData(data)
                }
            },
            //
            getProject() {
                this.axios.post("/sysCMSController/selectAllSysItem",{enable:1})
                    .then((res) => {
                        this.projectList = res.data.data;
                    }).catch((error) => {
                        console.log(error);
                    })
            },
            //
            getParentMenu() {
                this.axios.post("/menu/getMenuAll")
                    .then((res) => {
                        this.parentMenuLists = res.data.data;
                    }).catch((error) => {
                        console.log(error);
                    })
            },
            //
            selNewProject(value) {
                if(value) {
                    //
                    let data = {
                        itemId: value
                    }
                    this.axios.post("/menu/selectModuleByItemId", data)
                        .then((res) => {
                            if(res.data.data.length == 0){
                                this.nameList = [];
                            }else{
                                this.nameList = res.data.data;
                            }
                        }).catch((error) => {
                            console.log(error);
                        })
                    this.newBuild.moduleID = '';
                }
            },
            //
            addSAVE(formName) {
                this.$refs[formName].validate((valid) => {
                    if(valid) {
                        this.loadingNew = true;
                        //
                        let data = {
                            moduleId: this.newBuild.moduleID, //
                            itemId: this.newBuild.projectID, //id
                            parentId: this.newBuild.parentMenuID, //id
                            memo: this.newBuild.memo, //
                            status: this.newBuild.radio //
                        }
                        this.axios.post("/menu/saveMenu", data)
                            .then((res) => {
                                this.loadingNew = false;
                                if(res.data) {
                                    this.name = "";
                                    this.projectID = "";
                                    //
                                    this.total = 0;
                                    this.pageHandler(1);
                                    this.getParentMenu();
                                    this.dialogAdd = false;
                                    //
                                    this.$refs[formName].resetFields();
                                    this.parentMenuID = '';
                                    this.parentMenuName = '';
                                    this.$message({
                                        message: "",
                                        type: "success"
                                    });
                                } else {
                                    this.$message.error("");
                                }
                            }).catch((error) => {
                                console.log(error);
                                this.loadingNew = false;
                            });
                    } else {
                        console.log('error submit!!');
                        return false;
                    }
                });
            },
            //
            addOFF(formName) {
                this.dialogAdd = false;
                this.$refs[formName].resetFields();
                this.nameList = []
            },
            //
            selEditProject(value) {
                this.editDialog.moduleName = "";
                this.editDialog.moduleID = '';
                if(value == null || value == ''){
                }else{
                    //
                    let data = {
                        itemId: value
                    };
                    this.axios.post("/menu/selectModuleByItemId", data)
                        .then((res) => {
                            this.loadingEdit = false;
                            this.radioFlag = false;
                    		this.radioCon = "";
                            if(res.data.data.length == 0){
                                this.editNameList = [];
                            }else{
                                this.editNameList = res.data.data;
                                let that = this;
                                this.editNameList.forEach(function(val,key){
                                    if(val.id == that.editName){
                                        that.editDialog.moduleID = val.id; //id
                                        that.editDialog.moduleName = val.name; //
                                    }
                                });
                            }
                            this.editDialog.radio = this.radio;
                        }).catch((error) => {
                            console.log(error);
                        })
                    //
                    if(value == this.newEditID) {
                        this.newEditID = '';
                    } 
                    else {
                        this.editDialog.moduleName = "";
                        this.editDialog.moduleID = '';
                    }
                }
            },
            selRadio(value){
	            if(this.editDialog.moduleID == ""){
                    this.radioFlag = true;
                    this.radioCon = "";
                    this.editDialog.radio = 1;
                }else{
                    this.radioFlag = false;
                    this.radioCon = "";
                    if(value == 0){
                        this.axios.post("/menu/updateEnableById", {id: this.editDialog.moduleID})
                            .then((res) => {
                                if(res.data == false){
                                    this.editDialog.radio = 1;
                                }else{
                                    this.editDialog.radio = 0;
                                }
                            }).catch((error) => {
                                console.log(error);
                            })
                    }
                }
            },
            //
            edit(id) {
                this.dialogEdit = true;
                this.loadingEdit = true;
                //id
                this.menuID = id;
                let data = {
                    id
                };
                this.axios.post("/menu/selectMenuById", data)
                    .then((res) => {
                        //
                        this.editEcho(res,id);
                    }).catch((error) => {
                        console.log(error);
                        this.loadingEdit = false
                    });
            },
            //
            editSAVE(formName) {
                this.$refs[formName].validate((valid) => {
                    if(valid) {
                        this.loadingEdit = true;
                        let data = {
                            id: this.menuID, //id
                            moduleId: this.editDialog.moduleID, //
                            itemId: this.editDialog.projectID, //id
                            parentId: this.editDialog.parentMenuID, //id
                            memo: this.editDialog.memo, //
                            status: this.editDialog.radio //
                        }
                        this.axios.post("/menu/updateMenuById", data)
                            .then((res) => {
                            	this.radioFlag = false;
                    			this.radioCon = "";
                                this.loadingEdit = false;
                                if(res.data) {
                                    this.pageHandler(this.curPage)
                                    this.dialogEdit = false;
                                    this.$refs[formName].resetFields();
                                    this.$message({
                                        message: "",
                                        type: "success"
                                    });
                                } else {
                                    this.$message.error("");
                                }
                            }).catch((error) => {
                                console.log(error);
                            });
                    } else {
                        console.log('error submit!!');
                        return false;
                    }
                });
            },
            //
            editOFF(formName) {
                this.dialogEdit = false;
                this.$refs[formName].resetFields();
            },
            //
            del(id) {
                this.$confirm(', ?', '', {
                    confirmButtonText: '',
                    cancelButtonText: '',
                    type: 'warning'
                }).then(() => {
                    let data = {
                        id: id //id
                    };
                    this.axios.post("/menu/delMenuById", data)
                        .then((res) => {
                            if(res.data) {
                                this.pageHandler(this.curPage)
                                this.$message({
                                    type: 'success',
                                    message: '!'
                                });
                            } else {
                                this.$message.error("");
                            }
                        }).catch((error) => {
                            console.log(error);
                        });
 
                }).catch(() => {
                    this.$message({
                        type: 'info',
                        message: ''
                    });
                });
            },
            //
            editEmpty(){
            	this.radioFlag = false;
                this.radioCon = "";
                this.editNameList = [];
                this.menuID = ''; //id
                this.editDialog.projectName = ''; //
                this.editDialog.projectID = ''; //id
                this.editID = ''; //id
                this.newEditID = ''; //id
                this.editDialog.moduleName = ''; //
                this.editDialog.moduleID = ''; //id
                this.editName = ''; //id
                this.editDialog.parentMenuName = ''; //name
                this.editDialog.parentMenuID = ''; //id
                this.editDialog.radio = 1; //
                this.editDialog.memo = ''; //
            },
            //
            editEcho(res,id){
                this.menuID = res.data.id; //id
                this.editDialog.projectName = res.data.sysItemList.name; //
                this.editDialog.projectID = res.data.sysItemList.id; //id
                this.editID = res.data.sysItemList.id; //id
                this.newEditID = res.data.sysItemList.id; //id
                this.editName = res.data.moduleDetail.id; //id
                this.editNameID = res.data.moduleDetail.name;
                this.editDialog.moduleID = res.data.moduleDetail.id; //id
                this.editDialog.moduleName = res.data.moduleDetail.name; //
                this.editDialog.parentMenuName = res.data.parentMenu.name; //name
                this.editDialog.parentMenuID = res.data.parentMenu.id; //id
                this.radio = res.data.status; //  
                this.editDialog.memo = res.data.memo; //
                let data = {
                    menuId: id
                };
                this.axios.post("/menu/getMenuAll", data)
                    .then((res) => {
                        this.parentMenuList = res.data.data;
                    }).catch((error) => {
                        console.log(error);
                    })
            }
        },
        watch: {
            name: function() {
                this.searchStatus = 0
            }
        }
    }
</script>
<style lang="less" scoped="scoped">
    .jp-menuList {
        .menu-con {
            background: #fff;
            padding: 0 20px;
            box-shadow: 0 0 10px #9E9EA1;
            .title {
                font-size: 14px;
                font-weight: 400;
                color: #787878;
                padding: 14px 0 14px 14px;
                border-bottom: 1px solid #cccccc;
            }
            .menu-list {
                height: 88px;
                padding: 30px 0;
                line-height: 28px;
                position: relative;
                .item {
                    width: auto;
                    float: left;
                    .item-con {
                        margin: 0 14px;
                        text-align: center;
                        float: left;
                        font-size: 14px;
                        color: #333;
                    }
                    .item-ipt {
                        height: 28px;
                        float: left;
                        position: relative;
                        .el-input {
                            width: 140/32rem;
                            min-width: 140px;
                        }
                    }
                    button {
                        width: 58/32rem;
                        min-width: 60px;
                        height: 38px;
                        border-radius: 4px;
                        border: none;
                        color: #fff;
                        margin-top: -2px;
                        cursor: pointer;
                    }
                    .search {
                        background: #0190D4;
                        position: absolute;
                        top: 28px;
                        left: 75%;
                    }
                    .add {
                        background: #f33535;
                        position: absolute;
                        top: 28px;
                        left: 88%;
                    }
                }
            }
        }
        .data {
            height: 642px;
            background: #ffffff;
            margin-top: 30px;
            position: relative;
            table {
                width: 100%;
                font-size: 12px;
                position: relative;
                thead {
                    background: #abc2d7;
                    color: #ffffff;
                    height: 40px;
                    line-height: 40px;
                    td:nth-child(1) {
                        width: 62/32rem;
                    }
                    td:nth-child(2),
                    td:nth-child(3),
                    td:nth-child(4),
                    td:nth-child(5) {
                        width: 176/32rem;
                    }
                }
                tbody {
                    position: relative;
                    height: 510px;
                    tr {
                        height: 50px;
                        line-height: 50px;
                        color: #666666;
                        td {
                            height: 50px;
                            line-height: 50px;
                            border-bottom: 1px solid #cccccc;
                        }
                    }
                    tr:hover {
                        background: #EEF1F6;
                    }
                }
                td {
                    text-align: center;
                    border-right: 1px solid #cfd0d0;
                }
                td:last-child {
                    border-right: none;
                }
            }
            #page {
                width: 100%;
                position: absolute;
                bottom: 19px;
                text-align: center;
            }
        }
        .xinjian {
            p {
                color: #ff4949;
                position: absolute;
                top: 32px;
                height: 20px;
                line-height: 20px;
            }
        }
    }
</style>



